		wikiUrl: 'https://en.wikipedia.org/wiki/Black_and_Tan_Coonhound',
		dogtimeUrl: 'https://dogtime.com/dog-breeds/black-and-tan-coonhound',
		akcUrl: 'https://www.akc.org/dog-breeds/black-and-tan-coonhound/',
		petwaveUrl:
			'https://www.petwave.com/Dogs/Breeds/Black-and-Tan-Coonhound.aspx',
		purinaUrl: 'https://www.purina.com/dogs/dog-breeds/black-and-tan-coonhound',
